import React from 'react';
import './chart.scss';
import Chart from 'react-apexcharts';

class chart extends React.Component {
  state = {
    series: [
      {
        data: [],
      },
    ],
    options: {
      chart: {
        id: 'area-datetime',
        type: 'area',
        width: 600,
        height: 350,
        zoom: {
          autoScaleYaxis: true,
        },
        toolbar: {
          tools: {
            download: false,
            selection: false,
            zoom: false,
            zoomin: false,
            zoomout: false,
            pan: false,
            reset:
              false | '<img src="/static/icons/reset.png"  alt="" width="20">',
            customIcons: [],
          },
        },
      },
      dataLabels: {
        enabled: false,
      },
      markers: {
        size: 0,
        style: 'hollow',
      },
      xaxis: {
        show: false,
        type: 'datetime',
        tooltip: {
          enabled: false,
        },
        tickAmount: 4,
        labels: {
          show: false,
        },
        axisBorder: {
          show: false
      }
      },
      yaxis: {
        show: false,
      },
      colors: ['#ff9b22', '#ffc175'],
      tooltip: {
        x: {
          format: 'dd MMM yyyy',
        },
      },
      fill: {
        type: 'gradient',
        color: '#B1B9C4',
        gradient: {
          shadeIntensity: 1,
          opacityFrom: 0.7,
          opacityTo: 0.9,
          stops: [0, 100],
        },
      },
      grid: {
        show: false,
      },
    },
    selection: 'one_year',
    responsive: [{
      breakpoint: '600',
      options: {
        
      },
  }]
  };
  setChartOptions() {
    if (!this.props.chartColors) return;
    const colors = this.props.chartColors;
    this.setState((prevState) => ({
      options: { ...prevState.options, colors },
    }));
  }
  convertDataToChart() {
    const data = [];
    this.props.data.forEach((currData) => {
      currData.x *= 1000;
      data.push([currData.x, +currData.y]);
    });
    const series = [{ data }];
    this.setState({ series }, this.setChartOptions);
  }
  componentDidMount() {
    this.convertDataToChart();
  }


  render() {
    const { chartTitle, chartNum, chartSubTitle } = this.props;
    return (
      <div className="chart-container chart-content">
        <div className="chart-txt">
          <div className="coin-icon">
            <i className="fab fa-bitcoin"></i> Bitcoin (BTC)
          </div>
          <div className="curr-price-txt">{chartTitle}</div>
          <div className="curr-price-num">{chartNum}</div>
          <div className="curr-price-change">{chartSubTitle}</div>
        </div>
        <div className="toolbar">
        </div>

        <div id="chart-timeline">
          <Chart
            options={this.state.options}
            series={this.state.series}
            type="area"
            height={400}
            width={400}
          />
        </div>
      </div>
    );
  }
}

export default chart;
