import Navbar from './cmps/NavBar';
import HomePage from './pages/HomePage';
import Charts from './pages/Charts';
import ContactPage from './pages/ContactPage';
import SignupPage from './pages/SignupPage';
import ContactEditPage from './pages/ContactEditPage/ContactEditPage';
import ContactDetailsPage from './pages/ContactDetailsPage/ContactDetailsPage';
import UserMsg from './cmps/UserMsg';
import { loadUser } from './store/actions/userActions';
import { connect } from 'react-redux';
import 'antd/dist/antd.css';

import './assets/scss/global.scss';
import './App.scss';
import {
  HashRouter as Router,
  Redirect,
  Route,
  Switch,
} from 'react-router-dom';

import React, { Component } from 'react';

const PrivateRoute = (props) => {
  return props.isLoggedInUser ? (
    <Route {...props} />
  ) : (
    <Redirect to="/signup" />
  );
};

const SignUpRoute = (props) => {
  return props.isLoggedInUser ? <Redirect to="/" /> : <Route {...props} />;
};


class _App extends Component {
  get isConnect(){
    return window.navigator.onLine;
  }
  loadUser(){
    this.props.loadUser()
  }
  componentDidMount() {
    this.loadUser()
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.user !== this.props.user) this.loadUser()
  }
  render() {
    const { user } = this.props
    return (
      <div className="main-container">
        <Router>
          <Navbar />
          <main>
            <div className="gradient"></div>
            <Switch>
              <SignUpRoute
                isLoggedInUser={user}
                path="/signup/"
                component={SignupPage}
              />
              <PrivateRoute
                isLoggedInUser={user}
                path="/contact/edit/:id?"
                component={ContactEditPage}
              />
              <PrivateRoute
                isLoggedInUser={user}
                path="/contact/:id"
                component={ContactDetailsPage}
              />
              <PrivateRoute
                isLoggedInUser={user}
                path="/contact/"
                component={ContactPage}
              />
              <PrivateRoute
                isLoggedInUser={user}
                path="/charts/"
                component={Charts}
              />
              <PrivateRoute
                isLoggedInUser={user}
                path="/admin/"
                component={Charts}
              />
               <PrivateRoute isLoggedInUser={user} path="/" component={HomePage} />
            </Switch>
            <UserMsg isConnect={this.isConnect} />
          </main>
        </Router>
      </div>
    );
  }
}


function mapStateToProps(state) {
  return {
    user: state.userReducer.user,
  };
}

const mapDispatchToProps = {
  loadUser,
};
export const App = connect(mapStateToProps, mapDispatchToProps)(_App);
