import React, { Component } from 'react';
import { connect } from 'react-redux';
import { addMove, loadUser } from '../../store/actions/userActions';

import './TransferFund.scss';

class _TransferFund extends Component {
  state = {
    coins: '',
    errorMsg: ''
  };
  handleChange = ({ target }) => {
    this.setState({errorMsg: ''})
    this.setState({ coins: +target.value }, ()=> {
      if (this.state.coins > this.props.maxCoins) {
        this.setState({ errorMsg: 'Not enough BTC to send' });
        return;
      }
    });
  };
  onTransfer = async (ev) => {
    ev.preventDefault();
    if (!this.state.coins) {
      this.setState({ errorMsg: 'Cannot send 0 BTC' });
      return;
    }
    if (this.state.coins > this.props.maxCoins) {
      this.setState({ errorMsg: 'Not enough BTC to send' });
    }
    this.props.addMove(this.props.contact, this.state.coins)
    this.props.loadUser()
    this.setState({coins: ''})
  };
  render() {
    const { contact } = this.props;
    return (
      <div className="transfer-fund-container">
        <div className="transfer-title"> Transfer coins to {contact.name} </div>
        <div className="transfer-field">
          <div className="form-container">
            <form onSubmit={this.onTransfer}>
              <input
                type="number"
                value={this.state.coins}
                onChange={this.handleChange}
                name="amount"
                placeholder="Amount in BTC"
              />
              <button>Transfer</button>
              <span className="form-errors"> {this.state.errorMsg} </span>
            </form>
          </div>
        </div>
      </div>
    );
  }
}


const mapDispatchToProps = {
  addMove,
  loadUser
};
export default connect(null, mapDispatchToProps)(_TransferFund);
