import React, { Component } from 'react';
import UserService from '../../services/UserService';
import { connect } from 'react-redux';
import { loadUser } from '../../store/actions/userActions';

import './SignupPage.scss';

class _SignupPage extends Component {
  elInput = React.createRef();
  state = {
    user: {
      name: '',
      email: '',
    },
  };
  onSignup = async (ev) => {
    if (!this.state.user.name && !this.state.user.email) {
      this.setState({ errorMsg: 'Please Fill All Fields.' });
      return;
    }
    ev.preventDefault();
    await UserService.signup(this.state.user.name);
    this.moveToHome()
  };
  handleChange = ({ target }) => {
    const field = target.name;
    const value = target.type === 'number' ? +target.value : target.value;
    this.setState((prevState) => ({
      user: { ...prevState.user, [field]: value },
    }));
  };
  componentDidMount() {}
  moveToHome() {
    this.props.loadUser()
    this.props.history.push('/');
  }
  render() {
    const { name, email } = this.state.user;
    return (
      <div className="contact-edit-container">
        <div className="form-container">
          <div className="login-title">
            <h1>Signup</h1>
          </div>
          <form onSubmit={this.onSignup}>
            <label> Name</label>
            <input
              type="text"
              ref={this.elInput}
              value={name}
              onChange={this.handleChange}
              name="name"
            />
            <label> Email</label>
            <input
              type="text"
              value={email}
              onChange={this.handleChange}
              name="email"
            />
            <button>Save</button>
            <span className="form-errors"> {this.state.errorMsg} </span>
          </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    user: state.userReducer.user,
  };
}

const mapDispatchToProps = {
  loadUser,
};
export default connect(mapStateToProps, mapDispatchToProps)(_SignupPage);