import React, { Component } from 'react';
import UserService from '../../services/UserService';
import BitcoinService from '../../services/BitcoinService';
import Chart from '../../cmps/chart';
import ContentLoader from 'react-content-loader';
import { connect } from 'react-redux';
import { loadUser } from '../../store/actions/userActions';
import MovesList from '../../cmps/MovesList/MovesList';

import './HomePage.scss';

class HomePage extends Component {
  state = {
    user: '',
    btcRate: null,
    data: '',
    chartColors: ['#ff9b22', '#ffc175'],
    yesterdayRate: null,
  };
  async loadBtcRate() {
    try {
      const btcRate = await BitcoinService.getRate();
      this.setState({ btcRate });
    } catch {
      console.log('cant show user balance');
    }
  }
  get userUsdBalance() {
    const coins = this.state.user.coins;
    const rate = this.state.btcRate;
    const newNum = +(coins / rate).toFixed(2);
    return newNum.toLocaleString('en-US', { maximumFractionDigits: 2 });
  }
  get btcTousd() {
    const rate = +(1 / this.state.btcRate).toFixed(2);
    return rate.toLocaleString('en-US', { maximumFractionDigits: 2 });
  }
  get btcDiffAmount() {
    const res = (1/this.state.btcRate ) - (this.state.yesterdayRate)
    return res.toFixed(2)
  }
  get btcDiffPrecent() {
    const res = (this.btcDiffAmount / (1/this.state.btcRate)) * 100
    return res.toFixed(2)
  }
  async loadMarketPriceData() {
    try {
      const data = await BitcoinService.getMarketPrice();
      this.setState({ data });
    } catch {
      console.log('cannot pull data for chart');
    }
  }
  async loadYesterdayRate() {
    const yesterdayRate = await BitcoinService.getYesterdayRate();
    this.setState({ yesterdayRate });
  }
  get totalPaymentsUsd(){
    const sum = this.props.moves.reduce((acc, move)=>acc += move.amount, 0)
    const res = (1/this.state.btcRate)*sum
    return res.toLocaleString('en-US', { maximumFractionDigits: 2 });
  }
  componentDidMount() {
    this.loadYesterdayRate();
    const user = UserService.getUser();
    this.setState({ user });
    this.loadMarketPriceData();
    this.loadBtcRate();
  }
  render() {
    const { moves } = this.props;
    return (
      <div className="homepage-container container">
        {this.state.user && (
          <>
            <div className="welcome-back-txt">
              Welcome back {this.state.user.name}
            </div>
            <div className="main-content-hp">
              <div className="hp-main-cards balances">
                <div className="total-balances">
                  <div className="total-txt">Total Balances</div>
                  <div className="total-amount">{`$${this.userUsdBalance}`}</div>
                </div>
                <div className="balances-list">
                  <ul>
                    <li>
                      <div className="coin-text">
                        <i className="fab fa-bitcoin"></i> Bitcoin
                      </div>
                      <div className="coin-amounts">
                        {this.state.user.coins} BTC
                      </div>
                    </li>
                    <li>
                      <div className="coin-text">
                        <img
                          src="https://www.flaticon.com/svg/static/icons/svg/3358/3358745.svg"
                          width="20"
                          alt="logo"
                        />{' '}
                        LiteCoin
                      </div>
                      <div className="coin-amounts">
                        {this.state.user.coins.ltc
                          ? this.state.user.coins.ltc
                          : 0}{' '}
                        LTC
                      </div>
                    </li>
                    <li>
                      <div className="coin-text">
                        <i className="fab fa-bitcoin"></i> Bitcoin Cash
                      </div>
                      <div className="coin-amounts">
                        {this.state.user.coins.eth
                          ? this.state.user.coins.eth
                          : 0}{' '}
                        BCH
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
              <div className="hp-main-cards recent-payments">
                <div className="total-balances">
                  <div className="total-txt">Recent Payments</div>
                  <div className="total-amount">${this.totalPaymentsUsd}</div>
                </div>
                <div className="balances-list">
                  <MovesList hideTitle={true} moves={moves} />
                </div>
              </div>
              {!this.state.data.length ? (
                <div className="chart-container chart-content">
                  <ContentLoader
                    width={300}
                    height={300}
                    viewBox="-30 10 200 200"
                  >
                    <rect x="0" y="160" rx="0" ry="0" width="25" height="40" />
                    <rect x="30" y="145" rx="0" ry="0" width="25" height="55" />
                    <rect x="60" y="126" rx="0" ry="0" width="25" height="74" />
                    <rect x="90" y="80" rx="0" ry="0" width="25" height="120" />
                    <rect
                      x="120"
                      y="142"
                      rx="0"
                      ry="0"
                      width="25"
                      height="58"
                    />
                  </ContentLoader>
                </div>
              ) : (
                <Chart
                  type="marketPrice"
                  chartTitle="Current Price"
                  chartSubTitle={`${this.btcDiffAmount ? '+' : '-'}$${this.btcDiffAmount} (${this.btcDiffPrecent}%) today`}
                  chartNum={'$' + this.btcTousd}
                  data={this.state.data}
                  chartColors={this.state.chartColors}
                />
              )}
            </div>
          </>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    moves: state.userReducer.user.moves,
  };
}

const mapDispatchToProps = {
  loadUser,
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);
