
import React, { Component } from 'react'
import Chart from '../../cmps/chart'
import Loader from '../../cmps/Loader/Loader';
import BitcoinService from '../../services/BitcoinService';

import './Charts.scss'

class Charts extends Component {
    state = {
        user: '',
        btcRate: null,
        marketPriceData: [],
        avgBlockSizeData: [],
        tradeVolumeData: [],
        confirmedTransactionsData: [],
        chartColors: ['#ff9b22', '#ffc175'],
      };
      async loadBtcRate() {
        try {
          const btcRate = await BitcoinService.getRate();
          this.setState({ btcRate });
        } catch {
          console.log('cant show user balance');
        }
      }
      get btcTousd() {
        const rate = +(1 / this.state.btcRate).toFixed(2);
        return rate.toLocaleString('en-US', { maximumFractionDigits: 2 });
      }
      async loadMarketPriceData() {
        try {
          const marketPriceData = await BitcoinService.getMarketPrice();
          this.setState({ marketPriceData }, ()=> {
          });

        } catch {
          console.log('cannot pull data for chart');
        }
      }
      async loadAvgBlockSize() {
        try {
          const avgBlockSizeData = await BitcoinService.getAvgBlockSize();
          this.setState({ avgBlockSizeData });
        } catch {
          console.log('cannot pull avgBlockSizeData for chart');
        }
      }
      async loadTradeVolume() {
        try {
          const tradeVolumeData = await BitcoinService.getTradeVolume();
          this.setState({ tradeVolumeData });
        } catch {
          console.log('cannot pull avgBlockSizeData for chart');
        }
      }
      async loadConfirmedTransactions() {
        try {
          const confirmedTransactionsData = await BitcoinService.getConfirmedTransactions();
          this.setState({ confirmedTransactionsData });
        } catch {
          console.log('cannot pull avgBlockSizeData for chart');
        }
      }
      get showCharts(){
        if (this.state.marketPriceData.length > 1 &&    
          this.state.avgBlockSizeData.length > 1 &&    
          this.state.tradeVolumeData.length > 1 &&    
          this.state.confirmedTransactionsData.length > 1) return true
          else return false
      }
      componentDidMount() {
        this.loadMarketPriceData();
        this.loadAvgBlockSize();
        this.loadTradeVolume();
        this.loadConfirmedTransactions();
        this.loadBtcRate();
      }
    render() {
        return <div className="charts-container container">
           { !this.showCharts ? 
          <Loader/> : 
                <>
                <Chart
                type="marketPrice"
                chartTitle="Market Price (BTC/USD)"
                chartSubTitle=""
                chartNum={'$'+this.btcTousd}
                data={this.state.marketPriceData}
                chartColors={this.state.chartColors}
                />
                <Chart
                type="marketPrice"
                chartTitle="Average Block Size (MB)"
                chartSubTitle=""
                chartNum=""
                data={this.state.avgBlockSizeData}
                chartColors={['#2196f3', '#ffc175']}
                />
                <Chart
                type="marketPrice"
                chartTitle="Trade Volume"
                chartSubTitle=""
                chartNum=""
                data={this.state.tradeVolumeData}
                chartColors={['#4caf50', '#ffc175']}
                />
                <Chart
                type="marketPrice"
                chartTitle="Confirmed Transactions Per Day"
                chartSubTitle=""
                chartNum=""
                data={this.state.confirmedTransactionsData}
                chartColors={['#9c27b0', '#ffc175']}
                />
                </>
            } 
        </div>
    }
}

export default Charts
