import React, { Component } from 'react';
import ContactService from '../../services/ContactService';
import Avatar from 'react-avatar';
import TransferFund from '../../cmps/TransferFund';
import MovesList from '../../cmps/MovesList/MovesList';
import { connect } from 'react-redux';
import { loadUser } from '../../store/actions/userActions';
import { removeContact } from '../../store/actions/contactActions';
import { message } from 'antd';


import './ContactDetailsPage.scss';

class _ContactDetailsPage extends Component {
  state = {
    contact: null,
  };
  async loadContact(id) {
    try {
      const contact = await ContactService.getContactById(id);
      this.setState({ contact });
    } catch (err) {
      console.log('cannot pull contact', err);
    }
  }
  get contact() {
    return this.state.contact;
  }
  get maxCoins(){
    return this.props.user.coins 
  }
  onRemove = async (id) => {
    await this.props.removeContact(id)
    message.success('Contact has been removed successfully.');
    this.props.history.push('/contact');
  };
  componentDidMount() {
    const id = this.props.match.params.id;
    this.loadContact(id);
  }
  goBack = () => {
    this.props.history.push('/contact');
  };
  goEdit = () => {
    this.props.history.push(`/contact/edit/${this.state.contact._id}`);
  };
  render() {
    const { contact } = this.state;
    return (
      <div className="contact-details-container container">
        {contact && (
          <> 
          <div className="contact-details-content">
            <div className="contact-avatar">
              <Avatar size="30px" name={contact.name} round={true} />
            <div className="contact-name">{contact.name}</div>
            </div>
            <div className="contact-name">{contact.phone}</div>
            <div className="contact-name">{contact.email}</div>
            <TransferFund contact={contact} maxCoins={this.maxCoins}/>
            <MovesList contactId={contact._id} moves={this.props.user.moves} />
            <div className="details-btns">
              <button onClick={this.goBack}>Back</button>
              <button onClick={this.goEdit}>Edit</button>
              <button onClick={() => this.onRemove(this.state.contact._id)}> Delete </button>
            </div>
            </div>
          </>
        )}
        
      </div>
    );
  }
}


function mapStateToProps(state) {
  return {
    user: state.userReducer.user
  };
}

const mapDispatchToProps = {
  loadUser,
  removeContact
};
export default connect(mapStateToProps, mapDispatchToProps)(_ContactDetailsPage);
