import React, { Component } from 'react';
import './MovesList.scss';

export default class MovesList extends Component {
  componentDidMount() {}
  convertDate(date) {
    const newDate = new Date(date);
    const option = { year: 'numeric', month: 'numeric', day: 'numeric' };
    return newDate.toLocaleDateString('en-US', option);
  }
  get userMoves() {
    if (this.props.contactId) {
      return this.props.moves.filter(
        (move) => move.toId === this.props.contactId
      );
    } else {
      return this.props.moves;
    }
  }
  render() {
    return (
      <div className="move-list-container">
        <h1>{this.props.hideTitle ? '' : 'Your Moves'}</h1>
        {this.userMoves.length <= 0 && 'No moves yet.'}
        {this.userMoves.map((move, idx) => {
          return (
            <div key={idx} className="move-container">
              <div className="move-time">At: {this.convertDate(move.at)}</div>
              <div className="move-amount">Amount: {move.amount}</div>
            </div>
          );
        })}
      </div>
    );
  }
}
