import React, { Component } from 'react';
import ContactList from '../../cmps/ContactList';
import { connect } from 'react-redux';
import { loadContacts } from '../../store/actions/contactActions';
import './ContactPage.scss';
import ContactFilter from '../../cmps/ContactFilter/ContactFilter';

class ContactPage extends Component {
  state = {
    filter: {
      term: '',
    },
  };
  loadContacts() {
      this.props.loadContacts()
  }
  onFilter = (term) => {
    this.props.loadContacts({ term })
  };
  get contacts() {
    return this.props.contacts;
  }
  componentDidMount() {
    this.loadContacts();
  }

  render() {
    return (
      <div className="contact-page-container">
        <ContactFilter onFilter={this.onFilter} />
        {this.contacts && <ContactList contacts={this.contacts} />}
      </div>
    );
  }
}



function mapStateToProps(state) {
  return {
    contacts: state.contactReducer.contacts,
  };
}

const mapDispatchToProps = {
  loadContacts
};

export default connect(mapStateToProps, mapDispatchToProps)(ContactPage);
