import ContactService from '../../services/ContactService';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { message } from 'antd';
import {
  addContact,
  updateContact,
  getById,
} from '../../store/actions/contactActions';

import './ContactEditPage.scss';

class ContactEditPage extends Component {
  elInput = React.createRef();
  state = {
    contact: {
      name: '',
      email: '',
      phone: '',
      errorMsg: '',
    },
  };
  async loadContact(id) {
      if (!id) return;
    await this.props.getById(id);
    const contact = this.props.contact;
    this.setState({ contact });
  }
  onSaveContact = async (ev) => {
    if (
      !this.state.contact.name &&
      !this.state.contact.email &&
      !this.state.contact.phone
    ) {
      this.setState({ errorMsg: 'Please Fill All Fields.' });
      return;
    }
    ev.preventDefault();
    await ContactService.saveContact({ ...this.state.contact });
    message.success('Changes saved successfully.');
    this.props.history.push('/contact');
  };
  componentDidMount() {
    const id = this.props.match.params.id;
    id && this.loadContact(id)
  }
  handleChange = ({ target }) => {
    const field = target.name;
    const value = target.type === 'number' ? +target.value : target.value;
    this.setState(
      (prevState) => ({ contact: { ...prevState.contact, [field]: value } })
    );
  };
  render() {
    const { name, email, phone } = this.state.contact
    return (
      <div className="contact-edit-container">
        <div className="form-container">
            <form onSubmit={this.onSaveContact}>
              <label>Name</label>
              <input
                type="text"
                ref={this.elInput}
                value={name}
                onChange={this.handleChange}
                name="name"
              />
              <label>Email</label>
              <input
                type="text"
                value={email}
                onChange={this.handleChange}
                name="email"
              />
              <label>Phone</label>
              <input
                type="text"
                value={phone}
                onChange={this.handleChange}
                name="phone"
              />
              <button>Save</button>
              <span className="form-errors"> {this.state.errorMsg} </span>
            </form>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    contact: state.contactReducer.currEditContact,
  };
}

const mapDispatchToProps = {
  updateContact,
  addContact,
  getById,
};

export default connect(mapStateToProps, mapDispatchToProps)(ContactEditPage);
